import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ImageProvider } from '../providers/image/image';
import { LocationProvider } from '../providers/location/location';
import { HomePageModule } from '../pages/home/home.module';

import { AngularFireModule } from '@angular/fire';
import { AngularFirestoreModule } from '@angular/fire/firestore';
import { AngularFireStorageModule } from '@angular/fire/storage';
import { AngularFireAuthModule } from '@angular/fire/auth';
import { ENV } from './environment';
import { Camera } from '@ionic-native/camera';
import { Geolocation } from '@ionic-native/geolocation';


@NgModule({
  declarations: [
    MyApp,
    // HomePage,
  ],
  imports: [
    BrowserModule,
    HomePageModule,
    AngularFireModule.initializeApp(ENV.firebase, 'nasa-space-challenge'), // imports firebase/app needed for everything
    AngularFirestoreModule, // imports firebase/firestore, only needed for database features
    AngularFireAuthModule, // imports firebase/auth, only needed for auth features,
    AngularFireStorageModule, // imports firebase/storage only needed for storage features
    IonicModule.forRoot(MyApp, {
      preloadModules: true
    }, 
{
  links: [
    { loadChildren: '../pages/components/botton-menu-component/botton-menu-component.module.ngfactory#BottonMenuComponentPageModuleNgFactory', name: 'BottonMenuComponent', segment: 'botton-menu-component', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/index/index.module.ngfactory#IndexPageModuleNgFactory', name: 'IndexPage', segment: 'index', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/perfil/perfil.module.ngfactory#PerfilPageModuleNgFactory', name: 'PerfilPage', segment: 'perfil', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/rank/rank.module.ngfactory#RankPageModuleNgFactory', name: 'RankPage', segment: 'rank', priority: 'low', defaultHistory: [] }
  ]
}),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    // HomePage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    ImageProvider,
    LocationProvider,
    Geolocation,
    Camera
  ]
})
export class AppModule { }



